import { Button, Form, Space, Spin, Table } from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchCategoriesAsynk } from './CategoriesSlice';
import { useNavigate } from 'react-router-dom'
import { useForm } from 'react-hook-form';
import { ErrorMessage } from '@hookform/error-message';
import { CheckOutlined, PlusOutlined } from '@ant-design/icons';
import { addCategoryAsynk } from "./CategoriesSlice";
import { getCategories } from "../../../api/categories";

let datas = []

const Categories = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch();
    const [addNew, setAddNew] = useState(false)

    const [totalPage, setTotalPage] = useState(1)
    
    const [perPage, setPerPage] = useState(1)

    const { categories } = useSelector(data => data.categories);
   
    const categoryStatus = useSelector(data => data);

    const { register, handleSubmit, reset, formState: { errors, isDirty, isValid } } = useForm({
        mode: "onChange"
    });

    const onSubmit = async data => {
        fetchCategoriesAsynk("?page=1")
        await dispatch(addCategoryAsynk(data))
        reset();
    };

    const handleChange = (page) => {
         dispatch(fetchCategoriesAsynk(`?page=${page.current}`))
         console.log(page,"page");
    };


    useEffect(() => {
        dispatch(fetchCategoriesAsynk("?page=1"))
    }, [])

    useEffect(() => {
        if (categories.status === 200) {
            setTotalPage(categories.data.total)
            setPerPage(categories.data.per_page)
            datas = categories.data.data            
        } else {
            datas = []
        }
    }, [categories])

    const columns = [
        {
            title: 'Id',
            dataIndex: 'id',
        },
        {
            title: 'Tn ca hng',
            dataIndex: 'name',
                ellipsis: true,
        }
    ];

    if(categories){
        datas = categories.data?.data?.map((category, index)=>{
          return {
            key: index,
            id: category.id,
            name : category.name,
          }
        })
    }

    return (
        <div>
            {/* new */}
            <div className="">
                {!addNew ?
                    <button onClick={() => setAddNew(!addNew)} type="button" className="relative w-[200px] flex justify-center items-center px-5 py-2.5 font-medium tracking-wide text-white capitalize   rounded-md bg-[#001529]  focus:outline-none   transition duration-300 transform active:scale-95 ease-in-out">
                        <PlusOutlined />
                        <span className="pl-2 mx-1" >Thm mi</span>
                    </button>
                    :
                    <button onClick={() => {
                        setAddNew(!addNew)
                    }} type="button" className="relative w-[200px] flex justify-center items-center px-5 py-2.5 font-medium tracking-wide text-white capitalize   rounded-md bg-[#001529]  focus:outline-none   transition duration-300 transform active:scale-95 ease-in-out">
                        <CheckOutlined />
                        <span className="pl-2 mx-1" >Thm mi</span>
                    </button>
                }
            </div>
            {addNew &&
                <div className="m-auto w-[30%]">
                    <form onSubmit={handleSubmit(onSubmit)}>
                        <h1 className='text-center'>Thm mi danh mc</h1>
                        <div className="mt-5 bg-white shadow flex flex-col items-center space-y-3 p-3">
                            <input
                                placeholder="Nhp tn danh mc  y"
                                {...register("name", {
                                    required: "Khng c  trng",
                                    pattern: {
                                        value: /^[a-z/ ]+$/i,
                                        message: "Danh mc khng c cha s v k t c bit"
                                    },
                                    minLength: {
                                        value: 5,
                                        message: "Cn nhp t nht 5 k t"
                                    },
                                    maxLength: {
                                        value: 50,
                                        message: "Ch c nhp di 50 k t"
                                    }
                                })}
                                className=" text-white placeholder-gray-600 w-full px-4 py-2.5 mt-2 text-base   transition duration-500 ease-in-out transform border-transparent  bg-gray-200  focus:border-blueGray-500 focus:bg-white dark:focus:bg-gray-800 focus:outline-none focus:shadow-outline focus:ring-2 ring-offset-current ring-offset-2 ring-gray-400" />
                            <ErrorMessage errors={errors} style={{ color: "red" }} name="name" as="p" />
                            <button type="submit" disabled={!isDirty && !isValid} className="flex items-center px-5 py-1.5 font-medium tracking-wide capitalize outline-none hover:bg-gray-800  focus:outline-none focus:bg-gray-900  transition duration-300 transform active:scale-95 ease-in-out">
                                <span className="mx-1 text-gray-600 hover:text-white">Thm</span>
                            </button>
                        </div>
                    </form>
                </div>
            }

            {/* list */}
            <div className='my-6'>
                <h1 className='text-center'>Danh sch danh mc</h1>
            </div>
            <Spin spinning={categoryStatus.categories.loading}>
                <Table 
                    columns={columns} 
                    dataSource={datas} 
                    pagination={{
                        pageSize: perPage,
                        total: totalPage,
                    }}
                    onChange={(page) => handleChange(page)} 
                />
            </Spin>
        </div>
    )
}

export default Categories